interface OPropsCritEnumEntry {
	type?: 'sub'
	label: string,
	icon?: string,
	description?: string,
	enums: Dict<OPropsCritEnumEntry>
}

/** Recherche enum d'une props : paramÃ¨tres d'initialisation */
export interface OPropsCritEnum extends OPropsCrit {
	enums: Dict<OPropsCritEnumEntry>;
	/** false par dÃ©faut */
	multiple?: boolean;
	/** Recherche approchÃ©e (accents, ...). True par dÃ©faut */
	fuzzy?: boolean
	/** CrÃ©ation libre de la regExp de recherche */
	buildRegExp?: (searchTxt: string | string[], options?: {}) => RegExp
}

/** Recherche enum d'une props : area */
export class UiPropsCritEnumArea extends UiCritArea {

	constructor(id: string, protected params: OPropsCritEnum, libs?: IEndPoint | IEndPoint[]) {
		super(id, libs);
		this.setCritSgn("enum").setReplacePattern(/\benum\b/);
	}

	buildAbstract(xml: Element, ctx: OUiCritInit, options?: any): HTMLElement | undefined {
		const keys = xml ? (xml.getAttribute('uiTextSearch') || "").split("|") : [];
		const opts = xml.getAttribute("uiOptions") || "";
		const labels: string[] = [];
		keys.forEach((key) => {
			const dataEntry = this.params.enums && this.params.enums[key] ? this.params.enums[key] : null;
			labels.push(dataEntry ? dataEntry.label : key)
		});
		let valTag = <span class="critVal">{labels.join(", ")}</span>;
		return <span class="crit"><span class="critTitle">{this.getLabel(ctx)}</span> contient '{valTag}'</span>;
	}

	buildBody(ctx: OUiCritInit): ItemPropsCritEnum {
		let elt = new ItemPropsCritEnum(this.params);
		elt.critArea = this;
		return elt;
	}
}

/** Recherche enum d'une props : crit */
export class ItemPropsCritEnum extends ItemPropsCritBase {
	private _selectElt: HTMLSelectElement;

	constructor(protected param: OPropsCritEnum) {super();}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;

		this._selectElt = <select name={name} onchange={this.onChange}/> as HTMLSelectElement;
		this._selectElt.multiple = this.param.multiple ? true : false;

		function appendSubs(parent: Element, enums: Dict<OPropsCritEnumEntry>): void {
			for (const key in enums) {
				const data = enums[key];
				const option = data.type === 'sub' ?
					parent.appendChild(<optgroup label={data.label}/>) :
					parent.appendChild(<option value={key}>{data.label}</option>);
				if (data.description) option.title = data.description;
				if (data.type === 'sub')
					appendSubs(option, data.enums);
			}
		}

		appendSubs(this._selectElt, this.param.enums);
		const icon = this.critArea ? this.critArea.getIcon({reg: this.reg}) : null;
		const img = icon ? <img src={icon}/> : null;
		const head = sr.appendChild(<div class="critHead">
			{img}
			<span class="critTitle">{this.critArea ? this.critArea.getLabel({reg: this.reg}) : ""}</span>
			{this._selectElt}
			{this.insertAlternativesBtn(init)}
		</div>);
		UiCritBase.initHeadFromArea(this.critArea, {reg: this.reg}, head);
	}

	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		if (crit) {
			let v: string[] = [];
			if (this._selectElt.selectedOptions) {
				for (const o of this._selectElt.selectedOptions)
					v.push(o.value)
			}
			crit.setAttribute("uiTextSearch", v.join("|"));
		}
		return crit;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		let currentValue = xml ? (xml.getAttribute('uiTextSearch') || "").split("|") : [];
		for (const opt of this._selectElt.options) {
			opt.selected = currentValue.includes(opt.value) ? true : false;
		}
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const v: string[] = [];
		if (this._selectElt.selectedOptions) {
			for (const o of this._selectElt.selectedOptions)
				v.push(o.value)
		}
		let regExp: RegExp;

		if (this.param.buildRegExp)
			regExp = this.param.buildRegExp(v, {ignoreCase: false, wholeWord: true});
		else if (v.length > 0) {
			v.forEach((entry, pos, array) =>
				array[pos] = this.param.fuzzy === false ? LANG.escape4Regexp(entry) : LANG.escape4RegexpFuzzy(entry)
			);
			regExp = new RegExp(v.join("|"))
		}
		return regExp ? new SearchItemPropsRegexp(this.param.propName || this.critArea.getId(), regExp, this.param.multivalued) : undefined;
	}
}

