/** Recherche date d'une props : crit */
export class ItemPropsCritDate extends ItemPropsCritBase {
	protected _dateElt: InputPeriod;

	constructor(protected param: OPropsCritDate) {super();}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		let modes;
		switch (this.param.format) {
		case "future,past" :
			modes = InputPeriod.buildDefaultModes_futurAndPast();
			break;
		case "future" :
			modes = InputPeriod.buildDefaultModes_futur();
			break;
		}
		this._dateElt = new InputPeriod().initialize({defaultLabel: "Choisir une valeur...", modes});
		this._dateElt.addEventListener("change", this.onChange);
		this._dateElt.addEventListener("input", this.onChange);
		const icon = this.critArea ? this.critArea.getIcon({reg: this.reg}) : null;
		const img = icon ? <img src={icon}/> : null;
		const head = sr.appendChild(<div class="critHead">
			{img}
			<span class="critTitle">{this.critArea ? this.critArea.getLabel({reg: this.reg}) : ""}</span>
			{this._dateElt}
			{this.insertAlternativesBtn(init)}
		</div>);
		UiCritBase.initHeadFromArea(this.critArea, {reg: this.reg}, head);
	}

	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		if (crit) {
			if (crit && this._dateElt.isSpecified()) {
				crit.setAttribute("uiCurrentMode", this._dateElt.currentMode.getId());
				// Valeurs brutes (sans les regles d'exclusion/normalisation qui sont fournies par le "currentMode")
				if (this._dateElt.date1.valueAsNumber) crit.setAttribute("uiCurrentLess", this._dateElt.date1.valueAsNumber.toString());
				if (this._dateElt.date2.valueAsNumber) crit.setAttribute("uiCurrentMore", this._dateElt.date2.valueAsNumber.toString());
			}
		}
		return crit;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		this._dateElt.reset(xml.getAttribute("uiCurrentMode"), xml.hasAttribute("uiCurrentLess") ? Number.parseInt(xml.getAttribute("uiCurrentLess")) : null, xml.hasAttribute("uiCurrentMore") ? Number.parseInt(xml.getAttribute("uiCurrentMore")) : null);
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		if (this._dateElt.isSpecified())
			return SearchItemPropsDate.buildFromInputPeriod(this._dateElt, this.param.propName || this.critArea.getId(), this.param.multivalued);
		return undefined;
	}
}

customElements.define("wsp-itemcrit-props-date", ItemPropsCritDate);

