/** Recherche ref item (parents) */
export class ItemPropsCritRef extends UiCritBase {
	protected itemPtr: SrcPointerInline;

	constructor(protected param: OPropsCritRef) {super();}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		const sngs: string[] = [];

		if (this.reg.env.wsp.wspMetaUi) {
			this.param.whiteList.forEach((entry) => {
				const it = this.reg.env.wsp.wspMetaUi.getItemType(entry.itModel);
				if (it) sngs.push(LANG.escape4Regexp(it.getSgn()))
			});
		}
		this.itemPtr = <SrcPointerInline Ã®={{
			reg: this.reg,
			defaultAction: SrcPointerFastSelect.SINGLETON,
			sgnPattern: new RegExp("(" + sngs.join('|') + ")")
		} as OItemPointerInit}/> as SrcPointerInline;
		const icon = this.critArea ? this.critArea.getIcon({reg: this.reg}) : null;
		const img = icon ? <img src={icon}/> : null;
		const head = sr.appendChild(<div class="critHead">
			{img}
			<span class="critTitle">{this.critArea ? this.critArea.getLabel({reg: this.reg}) : ""}</span>
			{this.itemPtr}
			{this.insertAlternativesBtn(init)}
		</div>);
		UiCritBase.initHeadFromArea(this.critArea, {reg: this.reg}, head);
		this.itemPtr.onSrcRefChange.add(this.onChange);
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		const path = xml.getAttribute('path');
		if (path) {
			await this.itemPtr.setSrcRef(path);
			if (!this.itemPtr.shortDesc || ITEM.getSrcUriType(this.itemPtr.shortDesc.srcUri) != "item")
				this.itemPtr.setSrcRef(null);
		}
		return this;
	}

	async initFromInsAction(xml?: Element): Promise<this> {
		const path = xml ? xml.getAttribute('path') : null;
		if (path) {
			await this.itemPtr.setSrcRef(path);
			if (!this.itemPtr.shortDesc || ITEM.getSrcUriType(this.itemPtr.shortDesc.srcUri) != "item") {
				this.itemPtr.setSrcRef(null);
				await this.itemPtr.openFastSelect();
			}
		} else if (!this.itemPtr.srcRef) {
			await this.itemPtr.openFastSelect();
		}
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		if (this.itemPtr.shortDesc) {
			const whiteElt = this.param.whiteList.find((entry) => entry.itModel === this.itemPtr.shortDesc.itModel)
			const exp = new SearchItemLinks("LinkParents").setPath(this.itemPtr.srcRef);
			if (whiteElt && whiteElt.lnkTraits)
				exp.setLinkTraitFilter(whiteElt.lnkTraits.join('|'))
			return exp;
		}
	}

	protected getUiCritXmlAtt(): string {
		return this.critArea.getId();
	}
}

customElements.define("wsp-itemcrit-props-ref", ItemPropsCritRef);